import React from "react"

import { Button } from "reactstrap"

import { usePrefs } from "../../contexts/PreferencesContext"

import MDBModal from "./MDBModal"

const PreferencesForm = ({show, dismiss}) => {

    const {
        intvListStyle,
        setPref
    } = usePrefs()

    const handleInputChange = (event) => {
        const target = event.target
        const value = target.type === "checkbox" ? target.checked : target.value
        const name = target.name
        setPref(name, value)
    }

    const radioChangeHandler = (event) => {
        console.log(event.currentTarget)
    }

    return (
        <MDBModal
            title="User Preferences"
            show={show}
            dismiss={dismiss}
        >
            <div className="container">
                <div className="row">
                    <h5>Intervention List Style</h5>
                    <label>
                        <input
                            type="radio"
                            name="intvMenuStyle"
                            value="tabs"
                            checked={intvListStyle === "tabs"}
                            onChange={radioChangeHandler}
                        />
                        &nbsp; Tabs
                    </label>
                    <label>
                        <input
                            type="radio"
                            name="intvMenuStyle"
                            value="accordian"
                            checked={intvListStyle === "accordian"}
                            onChange={radioChangeHandler}
                        />
                        &nbsp; Accordian
                    </label>
                    <label>
                        <input
                            type="radio"
                            name="intvMenuStyle"
                            value="dropdown"
                            checked={intvListStyle === "dropdown"}
                            onChange={radioChangeHandler}
                        />
                        &nbsp; Dropdown
                    </label>
                </div>
                {/* <div className="row">
                    <label>
                        <input
                            type="checkbox"
                            name="dispAssessmentFindingsInline"
                            checked={dispAssessmentFindingsInline}
                            onChange={handleInputChange}
                        />
                        &nbsp; Embed Assessment Findings within Checklist
                    </label>
                </div>
                <div className="row">
                    <label>
                        <input
                            type="checkbox"
                            name="inhibitRaterOversharing"
                            checked={inhibitRaterOversharing}
                            onChange={handleInputChange}
                        />
                        &nbsp; Inhibit Rater Oversharing of Information
                    </label>
                </div> */}

                <Button
                    type="button"
                    className="btn btn-success"
                    onClick={dismiss}
                >
                    Dismiss
                </Button>
            </div>
        </MDBModal>
    )
}

export default PreferencesForm