import React from "react"

import {
    HashRouter as Router,
    Switch,
} from "react-router-dom"

import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth"

import "bootstrap/dist/css/bootstrap.min.css";

import "./styles.css";

import Scenario     from "./routes/Scenario"
import ScenarioList from "./routes/ScenarioList"

import UnauthedLayout from "./components/layouts/UnauthedLayout"

const App = (props) => {
    return (
        <AuthProvider>
            <Router>
                <Switch>
                    <PrivateRoute
                        path="/scenario/:scenarioId"
                        authFlowLayout={UnauthedLayout}
                        component={Scenario}
                    />
                    // default (catchall) route
                    <PrivateRoute
                        exact path="/"
                        authFlowLayout={UnauthedLayout}
                        component={ScenarioList}
                    />
                </Switch>
            </Router>
        </AuthProvider>
    )
}


export default App;