const getCheckList = (section) => {
    if (section.items) {
        return (
            <>
                <h4>{section.label} checklist items</h4>
                <CheckList steps={section.items} />
            </>
        )
    } else {
        return (
            <h4>There are no checklist items for this section</h4>
        )
    }
}

