const SectionLayout = ({section}) => {
    const { scenario, checkListItems, currentInterventionForm, setCurrentInterventionForm } = useContext(ScenarioContext)
    const [form, setForm] = useState(null)

    useEffect(() => {
        setCurrentInterventionForm(null)
    }, [section])

    useEffect(() => {
        if (null !== currentInterventionForm) {
            const f = scenario.interventionForms[currentInterventionForm]
            console.log(f)
            setForm(f)
        } else {
            setForm(null)
        }
    }, [currentInterventionForm])

    return (
        <Container fluid={true}>
            <Row>
                <Col md="6">
                        {getCheckList(section)}
                        <br/>
                        {/* <pre>
                    {JSON.stringify(section, null, 4)}
                    </pre> */}
                        {getInterventions(section)}
                </Col>
                <Col md="6">
                    {!!form &&
                        <IntvForm
                            id={form.id}
                            label={form.label}
                            checkedWhen={form.checkedWhen}
                            prompts={form.prompts}
                        />
                    }
                </Col>
            </Row>
        </Container>

    )
}

export default SectionLayout