import { useContext } from "react"

import {
    NavLink,
    Switch,
    useRouteMatch
 } from "react-router-dom"

import {
    Col,
    Container,
    Row
} from "reactstrap"

import { PrivateRoute } from "@meddbriefer/mdb-auth"

import UnauthedLayout from "./UnauthedLayout"
import { ScenarioContext } from "../../contexts/ScenarioContext"

// import MDBContainer from "./MDBContainer"
// import PhaseNav from "./PhaseNav"
// import CheckList from "./CheckList"
// import RaterInfo from "./RaterInfo"
import SectionLink from "../SectionLink"
// import BSI from "../routes/BSI"
import Phase from "../../routes/Phase"


const PhaseSubPhaseLayout = () => {
    const { scenario } = useContext(ScenarioContext)
    const { path, url} = useRouteMatch()
    const phases = scenario.phaseData.filter(rec => rec.uiNav === true)
    return (
        <Container fluid={true} className="main">
            <Row>
                <Col md="2" className="col">
                    <ul style={{padding: 0}}>
                        {phases.map(rec => (
                           <li key={rec.id} className={rec.type}>
                               {(rec.type === "phase" && !!rec.subPhases)
                                    ? <b> {rec.label}</b>
                                    : <SectionLink to={`${url}/${rec.id}`}> {rec.label} </SectionLink>
                               }
                           </li>
                        ))}
                    </ul>
                </Col>
                <Col md="10" className="col">
                    <Switch>
                        <PrivateRoute
                            exact path={`${path}/head-to-toe`}
                            authLayout={UnauthedLayout}
                            component={Phase}
                        />
                        <PrivateRoute
                            path={`${path}/:phaseID`}
                            authLayout={UnauthedLayout}
                            component={Phase}
                        />
                        <PrivateRoute
                            exact path={path}
                            authLayout={UnauthedLayout}
                        >
                            <h3>Please select a section on the left.</h3>
                        </PrivateRoute>
                    </Switch>
                </Col>
            </Row>
        </Container>
    )
}

